
import React, { PureComponent } from 'react'
import joinus from '../images/join.jpg'
import '../css/join.css'
import classnames from 'classnames'


export default class Contacts extends PureComponent {
  render() {
    let img_cls = classnames('join_code')
    return (
      <div>
        <h3>联系我们</h3>
        <p>微信招码：</p>
        <img className={img_cls} src={joinus} alt="联系我们"></img>
        <p>联系电话：13631446956</p>
        <p>电子邮箱：ouya603@163.com</p>
      </div>
    )
  }
}
