import React, { Component } from 'react'
import p1 from '../images/1.jpg'
import p2 from '../images/2.jpg'
import p3 from '../images/3.jpg'
import '../css/join.css'
import classnames from 'classnames'

class Home extends Component {
  render() {
    let img_cls = classnames('home_show')
    return (
      <div style={{textAlign:'center'}}>
        <img className={img_cls} src={p1} alt="未来小七 双11"></img>
        <img className={img_cls} src={p2} alt="未来小七 特卖"></img>
        <img className={img_cls} src={p3} alt="未来小七 双11特价"></img>
      </div>
    )
  }
}
export default Home;