import React, { Component } from 'react';
import './App.css';
import { Layout, Menu } from 'antd';
import 'antd/dist/antd.css';
import './css/goods.css';
import './Navi.css'
import Links from './Links'
import {BrowserRouter as Router, Route, NavLink, Link} from 'react-router-dom'
import Home from './components/Home';
import Aboutus from './components/Aboutus';
import Goods from './components/Goods';
import Service from './components/Service';
import Partner from './components/Partner';
import Contacts from './components/Contacts';
import Join from './components/Join';

const { Header, Footer, Content } = Layout;

     
                
const links = [{key:0, url:"http://www.jiandan.com", title:"简单搜索"},
{key:1, url:"http://www.baidu.com", title:"百度搜索"},
{key:2, url:"http://www.jd.com", title:"京东商城"},
{key:3, url:"http://www.alibaba.com", title:"阿里巴巴"},
{key:4, url:"http://www.dongfang.com", title:"东方财富"},
{key:5, url:"http://www.toutiao.com", title:"今日头条"}];

const menus = [
    {code:"/", name:"首页"},
    {code:"/about", name:"公司简介"},
    {code:"/product", name:"产品"},
    {code:"/service", name:"服务"},
    {code:"/contacts", name:"联系我们"},
    {code:"/partner", name:"合作伙伴"},
    {code:"/join", name:"加入我们"}
    ];


class App extends Component {
	 constructor(props) {
        super(props);
        let state = props;
        this.state = state;
    }

  render() {
    return (
        
     <div>
<Router>
    <Layout>
    
      <Header ><div className="logo"></div>
        <Menu
        theme="dark"
        mode="horizontal"
        defaultSelectedKeys={this.state.menu}
        style={{ lineHeight: '64px' }}
      >
	  {
		  menus.map((item, index) => { 
			  return (
			  <Menu.Item key={item.code} ><Link to={item.code} >{item.name}</Link></Menu.Item>
			  )
		  })
	  }
      </Menu>
</Header>
      <Content style={{ margin: '0 auto', width: '80%' }}>
     
        <Route exact path="/" component={Home} ></Route>
        <Route exact path="/home" component={Home} ></Route>
        <Route path="/product" component={Goods}></Route>
        <Route path="/about" component={Aboutus}></Route>
        <Route path="/service" component={Service}></Route>
        <Route path="/partner" component={Partner}></Route>
        <Route path="/contacts" component={Contacts}></Route>
        <Route path="/join" component={Join}></Route>
        
	  </Content>
      <Footer style={{ textAlign: 'center' }}>
	  <Links links={links}/>
	  Ant Design ©2016 Created by Ant UED</Footer>

    </Layout>
    </Router>
      </div>
      
    );
  }
}

export default App;
