import React, { PureComponent } from 'react'

const goods_list = [
    {name:"人工智能", goods:[{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100}]},
    {name:"生活电器", goods:[{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100}]},
    {name:"生产元件", goods:[{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100}]},
    {name:"儿童玩具", goods:[{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100}]},
    {name:"成人用品", goods:[{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100},{name:"xxx", price:100}]}
  ];

export default class Goods extends PureComponent  {

    constructor(props) {
        super(props);
        let state = props;
        state = {goods_list:goods_list};
        this.state = state
    }

    render() {

        let {goods_list=[], size = 10, start = 0 } = this.state;
		
        return (
            <div>
           {
                goods_list && goods_list.map((type, index) => {
                    var goods = type.goods.map((item, idx) => {
                        return(
                            <li class="list-item fore1">
                                <div class="p-img">
                                    <a href="//item.jd.com/100000177756.html" target="_blank" rel="noopener noreferrer">
                                        <img width="100" height="100" alt="" 
                                        src="//img11.360buyimg.com/n4/jfs/t1/5479/8/3546/70556/5b997c0cE40f64978/6540f1c2a6090257.jpg!q95.jpg"/>
                                    </a>
                                </div>
                                <a class="p-name" href="//item.jd.com/100000177756.html" target="_blank"  rel="noopener noreferrer"
                                title="Apple iPhone XR (A2108) 64GB 白色 移动联通电信4G手机 双卡双待 ">
                                Apple iPhone XR (A2108) 64GB 白色 移动联通电信4G手机 双卡双待 
                                </a>
                                <div class="p-price" data-price-id="100000177756">¥6499.00</div>
                            </li>
                        )
                    })
                    console.log("goods: " + goods);
                    return (
                        <div key={index}>
                            <h3 class="mt">{type.name}</h3>
                            <ul class="c-list">{goods}</ul>
                        </div>
                    )
                })
            
            }
            </div>
        )
    }
}
